import React, { useState, useEffect } from "react";
import { Button, Container, Image, Form, Grid, Segment } from 'semantic-ui-react'
import { useHistory } from "react-router-dom";
import axios from "axios";
import {Formik, Form as FormicForm, Field} from 'formik'
import * as Yup from 'yup'
import {Form as ValidForm,Input} from 'semantic-ui-react-form-validator'

export default function Singup(props) {
  const history = useHistory();

  const [user, setUser] = useState({}); // user info
  const [register, setRegister] = useState(true); // to show aleart
  const [image, setImage] = useState('https://res.cloudinary.com/bookwormcloud/image/upload/v1611699595/bookwormAvatar/cs4thogqix81sl7ybq4x.png')
  const [loading, setLoading] = useState(false)

  const uploadImage = async e => {
    const files = e.target.files
    const data = new FormData()
    data.append('file', files[0])
    data.append('upload_preset', 'bookwormAvatar')
    setLoading(true)
    axios.post('https://api.cloudinary.com/v1_1/bookwormcloud/image/upload', data)
      .then(res => {
        
        const file = res.data

        setImage(file.secure_url)
        setUser({ ...user, avatar: image });
        setLoading(false)
      }).catch(err => { console.log(err) })

  }

  //to add the input inside user
  const onChangeInput = ({ target: { name, value } }) => {
    setUser({ ...user, [name]: value ,avatar: image });
    console.log(user)
  };
  // to add the user info to database
  const onSubmit = (event) => {
    console.log("reg")
    event.preventDefault();
    if(!loading){
      
      axios
      .post("http://localhost:5000/api/user/register", user)
      .then((res) => {
        const user = res.data.user;
        
        if (user) {
          history.push("/login");
        } else {
          setTimeout(() => {
            setRegister(false);
          }, 1000);
        }
      })
      .catch((err) => console.log(err));
    }
  };

  return (
    <>
    
      <Container textAlign='center mt-5'>
      <Segment placeholder>
      
      <Grid columns={1} relaxed='very' stackable>
      <Grid.Column textAlign="center">
      {loading ? (
        <h3>Loading...</h3>
      ) : (
        <Image style={{border:"0.5px solid #8e8d8a"}} src={image} size='small' verticalAlign ="middle"  />
      )}
      </Grid.Column>
      {/* <Formik> */}
      <Grid.Column> 
      <Form >

           <Form.Input 
            icon='user'
            iconPosition='left'
            label='Name'
            placeholder='name'
            name='name'
            required={true}
            onChange={(e) => onChangeInput(e)}
          />

          <Form.Input 
           label='Choose your image'
           type='file'
           onChange={uploadImage}
          />
     
          <Form.Input 
            icon='at'
            iconPosition='left'
            label='Email'
            name='email'
            placeholder='email@example.com'
            onChange={(e) => onChangeInput(e)}
          />

          <Form.Input 
            icon='lock'
            iconPosition='left'
            label='Password'
            type='password'
            name='password'
            placeholder='Password'
            onChange={(e) => onChangeInput(e)}
          />      
           <Button
              variant="primary"
              type="submit"
              onClick={(e) => onSubmit(e)}
            >
              Signup
            </Button>
      </Form>
      </Grid.Column>
      {/* </Formik> */}
      </Grid>
      </Segment>
      </Container>
    </>
  );
}