import React, { useState } from 'react'
import { Button, Container, Form, Grid, Image, Segment } from 'semantic-ui-react'
import axios from "axios";
import { useHistory } from "react-router-dom";

export default function EditProfile(props) {

    const history = useHistory();
    const [user, setUser] = useState({}); // user info
    const [update, setUpdate] = useState()
    const [image, setImage] = useState('')
    const [loading, setLoading] = useState(false)
    const uploadImage = async e => {
        const files = e.target.files
        const data = new FormData()
        data.append('file', files[0])
        data.append('upload_preset', 'bookwormAvatar')
        setLoading(true)
        axios.post('https://api.cloudinary.com/v1_1/bookwormcloud/image/upload', data)
            .then(res => {
                const file = res.data
                setImage(file.secure_url)
                setUser({ ...user, avatar: image });
                setLoading(false)
            }).catch(err => { console.log(err) })

    }
    //to add the input inside user
    const onChangeInput = ({ target: { name, value } }) => {
        setUser({ ...user, [name]: value, avatar: image });
    };
    const onSubmit = (event) => {
        event.preventDefault();
        if (!loading) {
            axios.post("http://localhost:5000/api/user/update", user)
                .then((res) => {
                    const user = res.data.user
                    if (user) {
                        history.push("/profile");
                    } else {
                        setTimeout(() => {
                            setUpdate(false);
                        }, 1000);
                    }
                })
                .catch((err) => console.log(err));
        }
    };

    return (
        <>
            <Container textAlign='center mt-5'>
                <Segment placeholder>
                    <Grid columns={1} relaxed='very' stackable>
                        <Grid.Column textAlign="center">
                            {loading ? (
                                <h3>Loading...</h3>
                            ) : (
                                    <Image src={image} size='small' verticalAlign="middle" />
                                )}
                        </Grid.Column>
                        <Grid.Column>
                            <Form >
                                <Form.Input
                                    icon='user'
                                    iconPosition='left'
                                    label='Name'
                                    placeholder='name'
                                    name='name'
                                    required={true}
                                    onChange={(e) => onChangeInput(e)}
                                />
                                <Form.Input
                                    label='Choose your image'
                                    type='file'
                                    onChange={uploadImage}
                                />
                                <Form.Input
                                    icon='lock'
                                    iconPosition='left'
                                    label='Password'
                                    type='password'
                                    name='password'
                                    placeholder='Password'
                                    onChange={(e) => onChangeInput(e)}
                                />
                                <Button
                                    variant="primary"
                                    type="submit"
                                    onClick={(e) => onSubmit(e)} >Edit </Button>
                            </Form>
                        </Grid.Column>
                    </Grid>
                </Segment>
            </Container>
        </>
    )
}